import React from "react";

class Todo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            list: [{
                id: 1,
                checked: false,
                item: "History Essay",
                date: "2020-08-24",
                timeleft: this.countDown("2020-08-24")
            },{
                id: 2,
                checked: false,
                item: "PE Video",
                date: "2020-08-28",
                timeleft: this.countDown("2020-08-28")
            }]
        }
    }

    addItem(item, deadline) {
        if (item && deadline) {
            document.getElementById("additem").value = "";
            this.setState({
                list: this.state.list.concat([{
                    id: (this.state.list.length + 1),
                    checked: false,
                    item: item,
                    date: deadline,
                    timeleft: this.countDown(deadline)
                }])
            });
        } else {
            alert('Please enter something first');
        }
    }

    countDown(d){
        let today = new Date();
        let dd = String(today.getDate()).padStart(2, '0');
        let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
        let yyyy = today.getFullYear();
        today = yyyy + '-' + dd + '-' + mm;
        d = d.toString();
        d = d.split("-");
        dd = d[2] - dd;
        return (dd + " Days left");
    }

    render() {
        return (
            <div className={"box"}>
                <h2>To Do List</h2>
                <Additem value={this.state.list}
                         addItem={this.addItem.bind(this)}/>
                <div>
                    <Categories/>
                    <Items value={this.state.list}/>
                </div>
            </div>
        )
    }
}

class Additem extends React.Component {
    render() {
        return (
            <div>
                <div style={{display: "inline-flex"}}>
                    <div>
                        <div>
                            <label htmlFor="additem">Add Item: </label>
                            <input className={"input"} type="text" id="additem" name="additem"/>
                        </div>
                        <div>
                            <label htmlFor="deadline">Deadline: </label>
                            <input className={"input"} type="date" id="deadline" name="deadline"/>
                        </div>
                    </div>
                    <div>
                        <input style={{height: "100%"}} type="submit" value="Submit"
                               onClick={() => this.props.addItem(document.getElementById("additem").value, document.getElementById("deadline").value)}/>
                    </div>
                </div>
            </div>
        )
    }
}

const Counter = (props) => {
    return (
        <div className={"flex"}>
            <h3>Total Items: {props.value}</h3>
            <h3>Items Not Done: 0</h3>
            <span className={"button"}>
                    Remove Done
                </span>
        </div>
    )
}

const Categories = () => {
    return (
        <div className={"flex"}>
            <h3>Checked?</h3>
            <h3>Item</h3>
            <h3>Deadline</h3>
            <h3>Countdown</h3>
        </div>
    )
}

class Items extends React.Component{
    renderItems = () => {
        return this.props.value.map((element, index) => {
            return (
                <div key={index}>
                    <div className={"flex"}>
                        <input id={"check"} type={"checkbox"} />
                        <p>{element.item}</p>
                        <p>{element.date}</p>
                        <p>{element.timeleft}</p>
                    </div>
                    <hr />
                </div>
            )
        })
    }

    render() {
        return (
            <div>
                <div>
                    {this.renderItems()}
                    <Counter
                    value={this.props.value.length}/>
                </div>
            </div>

        )
    }
}

export default Todo;
