import React from "react";

const Desc = () => {
    return (
        <div>
            <a href="https://azuregitter.github.io/Lilac/">
                <span className={"button"}>
                        Fullscreen
                </span>
            </a>
            <h2 className={"title"}>Keyboard Controls:</h2>
            <h3>Enter - Confirm | X - Cancel | Shift - Run</h3>
            <h2 className={"title"}>Mobile Controls:</h2>
            <h3> Touch - Confirm | Two Finger Touch - Cancel </h3>
        </div>
    )
}

const Rpg = () => {
    return (
        <div className={"box"}>
            <h2>Lockdown at Lilac</h2>
            <iframe title={"Lockdown at Lilac RPG Game"} src="https://azuregitter.github.io/Lilac/"
                    className="game"/>
            <div className="changeOrientation">
                <h1>Change to Landscape</h1>
                <a href="https://azuregitter.github.io/Lilac/">Or click this to play fullscreen</a>
            </div>
            <Desc/>
        </div>
    )
}

export default Rpg;