import React from "react";
import {Link} from "react-router-dom";

class Home extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            growText: true,
        }
    }

    handleClick() {
        this.setState(state => ({
            growText: !state.growText
        }));
    }

    render() {
        return (
            <div className="box">
                <h2>Larong ginawa ko sa RPG Maker</h2>
                <div>
                    <Link to={'/rpg'}>
                        <div className="button">
                            Play Game!
                        </div>
                    </Link>
                    <div className={"button"}
                    onClick={() =>(this.handleClick())}>
                        {this.state.growText ? 'Shrink Gif' : 'Grow Gif'}
                    </div>
                </div>
                <div>
                    <img alt={"RPG Gif"}
                         src= {require('./assets/ataks.gif')}
                         id="gif"
                         width={this.state.growText ? '640px' : '320px'}
                         height={this.state.growText ? '480px' : '240px'}/>
                </div>
            </div>
        )
    }
}
export default Home;